:root {
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --dark-color: rgb(39, 36, 36);
  --dark-light-color: rgb(76, 76, 79);
  --gray-blue-color: rgb(179, 191, 208);
  --blue-color: rgb(120, 142, 171);
  --light-blue-color: rgb(233, 240, 243);
  --gray-color: rgb(139, 139, 139);
  --light-gray-color: rgb(217, 217, 217);
  --red-color: rgb(213, 33, 40);
  --dark-red-color: rgb(178, 75, 79);
  --green-color: rgb(71, 172, 52);
  --dark-green-color: rgb(42, 120, 50);
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/../fonts/Ubuntu-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/../fonts/Ubuntu-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/../fonts/Ubuntu-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: "Ubuntu", sans-serif;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: 100%;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header__block {
  display: flex;
  justify-content: space-between;
  padding: 35px 0;
}
.header__logo img {
  width: 165px;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__list {
  display: flex;
}
.nav__list li:not(:last-child) {
  padding-right: 44px;
}
.nav__item {
  font-size: 24px;
  font-weight: 500;
}
.nav__link {
  display: inline-block;
  font-weight: 500;
  color: var(--dark-color);
  transition: color 0.3s ease-in-out;
}
.nav__link:focus {
  outline-offset: 2px solid var(--dark-green-color);
  outline: 1px solid var(--dark-green-color);
}
.nav__link:hover {
  color: var(--dark-green-color);
}
.nav__link:active {
  color: var(--dark-green-color);
}

.social {
  display: flex;
  align-items: center;
}
.social__item:not(:last-child) {
  padding-right: 50px;
}
.social__link {
  display: block;
  width: 24px;
  height: 24px;
}
.social__link svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--dark-color);
  object-fit: cover;
  transition: fill 0.3s ease-in-out;
}
.social__link:focus {
  outline-offset: 2px solid var(--dark-green-color);
  outline: 1px solid var(--dark-green-color);
}
.social__link:hover svg {
  fill: var(--dark-green-color);
}
.social__link:active svg {
  fill: var(--dark-green-color);
}

.title {
  margin: 0;
  font-size: 50px;
  text-align: center;
  text-transform: capitalize;
  color: var(--dark-color);
  letter-spacing: 5px;
}
.title__hero {
  padding: 40px 0;
  font-family: "Ubuntu", sans-serif;
}

.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 100px;
}
.hero__logo img {
  width: 410px;
}
.hero__name {
  margin: 0;
  padding-top: 60px;
  text-transform: capitalize;
  text-align: center;
  font-size: 30px;
}
.hero__name1 {
  color: var(--dark-red-color);
  font-weight: 800;
}
.hero__name2 {
  color: var(--dark-light-color);
}
.hero__block {
  display: flex;
  justify-content: center;
  padding-top: 30px;
  text-transform: capitalize;
  font-size: 28px;
  color: var(--dark-color);
}
.hero__work {
  padding: 0 50px;
}
.hero__stick {
  border: 1px solid var(--dark-color);
}

.examples-item__link {
  position: relative;
  display: block;
  overflow: hidden;
  line-height: 300%;
  font-weight: 300;
  font-size: 32px;
  color: var(--dark-color);
  background-color: transparent;
  transition: color 0.3s ease-in-out;
}
.examples-item__link:hover {
  color: var(--light-color);
}
.examples-item__link:hover .examples-item__bg {
  opacity: 1;
  transform: scaleY(1);
}
.examples-item__bg {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  opacity: 0;
  transform: translateY(-100%);
  transition: transform 0.5s cubic-bezier(0.77, 0.125, 0.265, 1.04), opacity 0.5s cubic-bezier(0.77, 0.125, 0.265, 1.04);
  will-change: transform;
  pointer-events: none;
}
.examples-item__container {
  position: relative;
  padding-top: 50px;
  padding-bottom: 50px;
  z-index: 5;
}
.examples-item__title {
  margin: 0;
  font-weight: 500;
  font-size: 64px;
}

.examples__list li:not(:last-child) {
  border-bottom: 1px solid var(--dark-color);
}
.examples__title {
  padding: 50px 0;
  font-size: 32px;
  letter-spacing: 3px;
  color: var(--dark-color);
}
.examples__border {
  border-bottom: 1px solid var(--light-gray-color);
}

.communication {
  padding: 150px 0;
}
.communication__git {
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  font-size: 24px;
  line-height: 150%;
  color: var(--dark-color);
}
.communication__git svg {
  width: 40px;
  height: 40px;
}
.communication__title {
  margin: 0;
  font-size: 32px;
  font-weight: 500;
  letter-spacing: 3px;
  line-height: 150%;
}
.communication__link {
  font-size: 54px;
  font-weight: 500;
  line-height: 150%;
  transition: color 0.3s ease-in-out;
}
.communication__link:hover {
  color: var(--red-color);
}
.communication__container {
  display: flex;
  flex-direction: column;
}

.telegram-link {
  color: var(--dark-color);
  font-size: 24px;
  font-weight: 500;
  line-height: 150%;
}
.telegram-link__span {
  color: var(--red-color);
  transition: color 0.3s ease-in-out;
}
.telegram-link__span:hover {
  color: var(--dark-red-color);
}

.hero-about {
  background-color: inherit;
}
.hero-about__img {
  width: 70%;
  height: auto;
}
.hero-about__container {
  display: flex;
  align-items: center;
}
.hero-about__txt {
  padding-right: 20px;
}
.hero-about__title {
  font-size: 36px;
}
.hero-about__subtitle {
  font-weight: 500;
  font-size: 32px;
  color: var(--dark-light-color);
}
.hero-about__descr {
  line-height: 170%;
  font-weight: 300;
  font-size: 24px;
}

.skills {
  background-color: inherit;
  padding-top: 100px;
}
.skills__list {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  margin: 0 auto;
  gap: 20px;
}

.skills-element {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.skills-element__circle {
  --progress-stroke: #000;
  --progress-bg-opacity: 0.3;
  position: relative;
  width: 100px;
  height: 100px;
}
.skills-element__circle svg {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.skills-element__circle .progress {
  stroke: var(--progress-stroke);
  transform: rotate(-90deg) translateX(20px);
  transform-origin: center;
}
.skills-element__circle .progress-bg {
  stroke: var(--progress-stroke);
  opacity: var(--progress-bg-opacity);
}
.skills-element__circle--one-color {
  --progress-stroke: #E44D26;
}
.skills-element__circle--two-color {
  --progress-stroke: #1572b6;
}
.skills-element__circle--three-color {
  --progress-stroke: var(--dark-color);
}
.skills-element__circle--four-color {
  --progress-stroke: #0868AC;
}
.skills-element__circle--five-color {
  --progress-stroke: var(--dark-color);
}
.skills-element__circle--six-color {
  --progress-stroke: #00759D;
}
.skills-element__circle--seven-color {
  --progress-stroke: #0073BB;
}
.skills-element__circle--eight-color {
  --progress-stroke: #e48e00ff;
}
.skills-element__circle--nine-color {
  --progress-stroke: #FFE634;
}
.skills-element__value {
  position: absolute;
  left: 50%;
  top: 50%;
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  color: var(--dark-light-color);
  transform: translate(-50%, -50%);
}
.skills-element__icon {
  padding-bottom: 20px;
}
.skills-element__icon img {
  width: 120px;
  height: 120px;
}

.example {
  color: var(--dark-color);
}
.example__title {
  margin: 0;
  font-weight: 500;
  font-size: 64px;
  text-transform: capitalize;
  line-height: 150%;
}
.example__list {
  counter-reset: select;
}
.example__subtitle {
  margin: 0;
  line-height: 150%;
  font-weight: 500;
  font-size: 32px;
}
.example__link {
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  letter-spacing: 5px;
  color: var(--red-color);
  transition: color 0.3s ease-in-out;
}
.example__link:hover {
  color: var(--dark-red-color);
}
.example__shift {
  text-align: center;
  font-weight: 500;
  font-size: 24px;
  border-bottom: 1px solid var(--dark-color);
  padding-top: 30px;
  padding-bottom: 30px;
}
.example__shift a {
  cursor: pointer;
}
.example__image {
  padding: 50px 0;
}

.example-item {
  padding: 40px 0;
}
.example-item__title::before {
  counter-increment: select;
  content: counter(select);
  position: absolute;
  left: 0;
  right: 0;
  top: 10px;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 34px;
  height: 34px;
  font-weight: 700;
  font-size: 20px;
  line-height: 40px;
  color: var(--light-color);
  background-color: var(--red-color);
}
.example-item__title {
  position: relative;
  padding-left: 70px;
  font-size: 32px;
  line-height: 150%;
}
.example-item__descr {
  padding-left: 70px;
  font-size: 24px;
  font-weight: 300;
  line-height: 200%;
}

.example-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 100px;
}
.example-block__title {
  text-align: center;
  font-weight: 500;
  font-size: 32px;
  line-height: 144px;
  color: var(--gray-color);
}

.footer {
  margin: 0;
  padding: 70px 0;
  border-top: 1px solid var(--blue-color);
  background-color: var(--light-blue-color);
}
.footer__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__address-block {
  color: var(--blue-color);
  font-weight: 400;
}
.footer__address a {
  font-weight: 700;
  color: var(--dark-color);
  transition: color 0.3s ease-in-out;
}
.footer__address a:hover {
  color: var(--dark-red-color);
}